import React, { FunctionComponent } from "react"
import { StaticImage, GatsbyImage, getImage, IGatsbyImageData } from "gatsby-plugin-image"
import { prettifyExcerpt } from 'services/tools'
import Skeleton from "react-loading-skeleton"

/**
 * Settings
 */
const skeletonDuration = 5

const CardCoverAttrs = {
  className: "h-100 w-100 image-cover",
  alt: ""
}

/**
 * Type
 */
interface IArticlePreview {
  cover?: IGatsbyImageData,
  title?: string,
  excerpt?: string,
  link?: string,
  skeletonMode?: boolean,
}

interface ICardCover{
  image?: IGatsbyImageData, 
  skeletonMode?: boolean
}

interface ICardTitle {
  title?: string, 
  skeletonMode?: boolean
}

interface ICardText {
  excerpt?: string, 
  skeletonMode?: boolean
}

interface ICardLink {
  link?: string, 
  skeletonMode?: boolean
}

/**
 * Atoms card cover
 */
const CardCover:FunctionComponent<ICardCover> = ({ image, skeletonMode }) => {
  if (skeletonMode) 
    return <StaticImage src="../assets/images/default_soon.png" {...CardCoverAttrs} />
  
  if (image) 
    return <GatsbyImage image={image} {...CardCoverAttrs} />

  return <StaticImage src="../assets/images/default.png" {...CardCoverAttrs} />
}

/**
 * Atoms card title
 */
const CardTitle:FunctionComponent<ICardTitle> = ({ title, skeletonMode }) => {
  if (!title && !skeletonMode) return <></>

  return (
    <h3 className="h4 mb-2">
      {skeletonMode ? (
        <Skeleton width="12em" height="1em" duration={skeletonDuration} />
      ) : (
        title
      )}
    </h3>
  )
}

/**
 * Atoms card text
 */
const CardText:FunctionComponent<ICardText> = ({ excerpt, skeletonMode }) => {
  if (!excerpt && !skeletonMode) return <></>

  if (skeletonMode) return (
    <div className="text-small">
      <Skeleton count={3} duration={skeletonDuration} />
    </div>
  ) 

  return (
    <div className="text-small">
      {prettifyExcerpt(excerpt)}
    </div>
  )
}

/**
 * Atoms card link
 */
const CardLink:FunctionComponent<ICardLink> = ({ link, skeletonMode }) => {
  if (!link || skeletonMode) return <></>

  return (
    <a href={link} className="stretched-link d-inline-block mt-2 mb-0">
      Lire l'article
    </a>
  )
}

/**
 * View
 */
const ArticlePreview:FunctionComponent<IArticlePreview> = ({ 
  title, 
  cover, 
  excerpt, 
  link, 
  skeletonMode
}) => (
  <div className="row no-gutters shadow rounded position-relative" style={{ minHeight: '15rem' }}> 
    <div className="col-12 col-md-5">
      <CardCover image={cover && getImage(cover)} skeletonMode={skeletonMode} />
    </div>
    <div className="col-12 col-md-7 p-3 position-static">
      <CardTitle title={title} skeletonMode={skeletonMode} />
      <CardText excerpt={excerpt} skeletonMode={skeletonMode} />
      <CardLink link={link} skeletonMode={skeletonMode} />
    </div>
  </div> 
)

export default ArticlePreview
