import React, { FunctionComponent } from "react";
import { graphql } from "gatsby";
import Layout from "partials/layouts/layout-page";
import PostsList from "partials/common/postsList";

/**
 * Type
 */
interface ICategoryTemplate {
  data: IAllMdxData,
  pageContext: any
}

/**
 * QUery
 */
export const pageQuery = graphql`
  query categoryPageQuery($category: String!) {
    allMdx(
      sort: { fields: [frontmatter___date], order: ASC }
      filter: {
        frontmatter: { category: { eq: $category } }
        fields: { group: { eq: "blog" } }
      }
    ) {
      nodes {
        frontmatter {
          title
          date
          category
          cover {
            childImageSharp {
              gatsbyImageData(layout: FULL_WIDTH)
            }
          }
        }
        fields {
          slug
          group
        }
        excerpt(pruneLength: 150)
      }
    }
  }
`;


/**
 * View
 */
const CategoryTemplate:FunctionComponent<ICategoryTemplate> = ({ data, pageContext }) => {
  // format mdx data
  const posts = data?.allMdx?.nodes || null;

  // title
  const pageTitle = pageContext?.category || null;

  // render
  return (
    <Layout>
      <div className="container pt-2 mt-4 mb-3">
        {pageTitle && (
          <h1 className="mb-4 effect-square">
            Cat√©gorie : {pageTitle}
          </h1>
        )}
        {posts.length > 0 && (
          <PostsList posts={posts} />
        )}
      </div>
    </Layout>
  );
}


export default CategoryTemplate